import { getBoolean, getDateFrom, getDateTo, getRandom } from '../utils';
export const createPoint = (type, destination, offers) => {
    const dateFrom = getDateFrom();
    const dateTo = getDateTo(dateFrom);
    return {
        id: crypto.randomUUID(),
        price: getRandom(1000 /* Price.Min */, 2000 /* Price.Max */),
        dateFrom,
        dateTo,
        destination,
        favorite: getBoolean(),
        offers,
        type
    };
};
