import { getRandom } from '../utils';
import { VALUE } from '../const';
const mockOfferItem = (type) => {
    const price = getRandom(50 /* Price.Min */, 80 /* Price.Max */);
    return {
        id: crypto.randomUUID(),
        title: `Offer of ${type}`,
        price
    };
};
export const createOffers = (type) => ({
    type,
    offers: Array.from({ length: getRandom(0, VALUE) }, () => mockOfferItem(type))
});
